// IMPORTS
// Material UI
import AppBar from '@material-ui/core/AppBar'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import DeleteIcon from '@material-ui/icons/Delete'
import BackIcon from '@material-ui/icons/ArrowBack'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import PropTypes from 'prop-types'

// Internal imports
import styles from '../../../resources/theme/default'
import EnhancedComponent from '../../components/EnhancedComponent'
import { edit, fetchFleet, update, destroy } from '../FleetActions'
import { getEditingFleet } from '../FleetReducer'
import { Grid } from '@material-ui/core';

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class DeleteFleet extends EnhancedComponent {
  componentDidMount() {
    this.props.edit(this.props.match.params.id)
  }

  onSubmit = () => {
    const { fleet } = this.props
    this.props.destroy(fleet.id).then(data => {
        console.log(data)
        if (data && data.code === 200) {
            this.props.history.push('/fleet')
        }
    })
  }

  onClose = () => {
    this.props.history.push('/fleet')
  }

  render() {
    const { classes, fleet } = this.props
    return (
      <Dialog
        open={fleet !== null}
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Excluir Frotista
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Typography>
          <Grid container spacing={6} Style="padding: 20px">
            <Grid item xs>
                <Button
                  onClick={() => this.onSubmit()}
                  className={classes.colorDanger}
                  size="medium"
                  //color="secondary"
                  variant="contained"
                >
                  <DeleteIcon className={classes.btnIcon} /> Confirmar
                </Button>
            </Grid>

            <Grid item xs>
              <Button
                onClick={() => this.onClose()}
                size="medium"
                color="primary"
                variant="contained"
              >
                <BackIcon className={classes.btnIcon} /> Cancelar
              </Button>
            </Grid>
          </Grid>
        </Typography>
      </Dialog>
    )
  }
}

DeleteFleet.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  roles: state.fleet.roles || [],
  fleet: getEditingFleet(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      update,
      destroy,
      edit,
      fetchFleet,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DeleteFleet))
